* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 3% 25%, #1A7C8D 0%, #01032B 100%);
    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 3% 25%, #1A7C8D 0%, #01032B 100%);
    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 3% 25%, #1A7C8D 0%, #01032B 100%);
    /* ie 10+ */
    background:-ms-radial-gradient(circle at 3% 25%, #1A7C8D 0%, #01032B 100%);
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, #1A7C8D 0%, #01032B 100%);
}

.gradient__text {
    background: var(--gradient-text);
}